<!-- 컴포넌트 ui 정의 -->
<!-- root element가 하나만 있어야함 -->
<template>
  <v-card>
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">회원 정보 수정</span>
    </v-card-title>
    <form @submit.prevent="submit, cancel">
      <v-text-field v-model="profile.member_id" :readonly="true"></v-text-field>
      <v-text-field
        v-model="profile.password"
        :counter="10"
        :error-messages="errors"
        label="비밀번호"
        required
        :rules="user_pw_rule"
      ></v-text-field>
      <v-text-field
        v-model="profile.tel"
        :counter="13"
        :error-messages="errors"
        label="Phone Number"
        required
      ></v-text-field>
      <v-text-field
        v-model="profile.email"
        :error-messages="errors"
        label="Email"
        required
        readonly
      ></v-text-field>
      <v-text-field
        v-model="profile.addr"
        :counter="100"
        :error-messages="errors"
        label="Address"
        required
      ></v-text-field>
      <v-text-field
        v-model="profile.zipcode"
        :counter="10"
        :error-messages="errors"
        label="Zipcode"
        required
      >
      </v-text-field>
      <v-text-field
        v-model="profile.detail_addr"
        :counter="10"
        :error-messages="errors"
        label="detail_address"
        required>
      </v-text-field>
    <v-col class="text-right">
      <v-btn class="mr-2 navy white--text" @click="submit()">변경</v-btn>
      <v-btn class="navy white--text" @click="cancel()">취소</v-btn>
    </v-col>
    </form>

    <!-- 기본적으로 버튼과 버튼 사이에 마진이 들어감 -->
  </v-card>
</template>

<script>
import apiMember from "@/apis/member";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Profile",
  // 추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  // return 값이 되려면 괄호로 감싸야함
  data() {
    return {
      profile: {},
    };
  },

  //컴포넌트에서 사용하는 메소드 정의
  methods: {
    submit() {
      if (this.profile.password == "") {
        alert("비밀번호를 입력해주세요");
        return;
      } else {
        apiMember.updateMember(this.profile);
        this.$router.push("/");
      }
    },
    cancel() {
      this.$router.push("/");
    },
  },
  created() {
    this.profile.id = this.$store.state.userId;
    // 로그인이 안된 상태라면 홈으로 던짐
    if (this.$store.state.userId == null || this.$store.state.userId == "") {
      this.$router.push("/login");
    } else {
      apiMember.getMember().then((response) => {
        if (response.data.result === "success") {
          this.profile = response.data;
          console.log(response.data);
        } else {
          alert("잘못된 토큰 입니다");
          this.$router.push("/login");
        }
      });
    }
  },
};
</script>
<!-- 컴포넌트 -->
<style scoped></style>
